import { Injectable } from '@angular/core';
import { Http, RequestOptions, Response } from '@angular/http';
import { Product } from './product';
import { Weather } from './weather';
import 'rxjs/add/operator/toPromise';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/map'

@Injectable()
export class ProductService {
  private productUrl = './assets/sample/sampleProduct.json';  // URL to web api
  private weatherUrl = 'http://api.openweathermap.org/data/2.5/weather?q=Riyadh,ksa&appid=a809c777d6813b6b0905a9a7bf1a8399';  // URL to web api


  constructor(private http: Http) { }

  getProduct(): Observable<any> {
    return this.getJSON(this.productUrl);
  }
  getWeather(): Observable<any> {
    return this.getJSON(this.weatherUrl);

  }
  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error);
    return Promise.reject(error.message || error);
  }

  public getJSON(url): Observable<any> {
    return this.http.get(url)
      .map((response: Response) => {
        //console.log(url+ " ::: " + response.json());
        //console.log(response)
        return response.json();
      })
  }


}



