import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule }    from '@angular/http';
import { AppComponent } from './app.component';
import { MenuComponent } from './menu/menu.component';
import { ProductComponent } from './product/product.component';
import { UtilityComponent } from './utility/utility.component';
import { FooterComponent } from './footer/footer.component';
import { ProductDisplayComponent } from './product/product-detail/product-detail.component';
import { ProductFilterCostComponent } from './product/product-filter-cost/product-filter-cost.component';
import { ProductFilterColorComponent } from './product/product-filter-color/product-filter-color.component';
import { ProductFilterTypeComponent } from './product/product-filter-type/product-filter-type.component';
import { ProductFilterTextComponent } from './product/product-filter-text/product-filter-text.component';
import { EmailComponent } from './email/email.component';
import { UtilitySocialmediaComponent } from './utility/utility-socialmedia/utility-socialmedia.component';
import { UtilityWeatherComponent } from './utility/utility-weather/utility-weather.component';
import { UtilityNewsComponent } from './utility/utility-news/utility-news.component';
import { ProductOverviewComponent } from './product/product-overview/product-overview.component';
import { ProductService } from './services/product.service';
import {SearchproductService} from './services/searchproduct.service';
import { NouisliderComponent } from 'ng2-nouislider';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatChipsModule,MatButtonModule } from '@angular/material';

@NgModule({
  declarations: [
    AppComponent,
    MenuComponent,
    ProductComponent,
    UtilityComponent,
    FooterComponent,
    ProductDisplayComponent,
    ProductFilterCostComponent,
    ProductFilterColorComponent,
    ProductFilterTypeComponent,
    ProductFilterTextComponent,
    EmailComponent,
    UtilitySocialmediaComponent,
    UtilityWeatherComponent,
    UtilityNewsComponent,
    ProductOverviewComponent,
    NouisliderComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    BrowserAnimationsModule,
    MatChipsModule,
    MatButtonModule
  ],
  exports : [],
  providers: [ProductService, SearchproductService],
  bootstrap: [AppComponent]
})
export class AppModule { }



