import { Component, OnInit,ViewChild } from '@angular/core';

import { SearchproductService } from '../../services/searchproduct.service';
import { Product } from '../../services/product';


@Component({
  selector: 'app-product-filter-cost',
  templateUrl: './product-filter-cost.component.html',
  styleUrls: ['./product-filter-cost.component.css']
})
export class ProductFilterCostComponent implements OnInit {
  @ViewChild('sliderRef') sliderRef;
  
  constructor(private searchproductService: SearchproductService) { }
  public someRange:Number[]=[ 0, 4000 ]
 
  ngOnInit() {
    //console.log("inside ngOnInit event")
  
  }
  public onChange(event:any){
    //console.log("inside onChange event : "+this.sliderRef.slider.get());
    this.searchproductService.setRange(this.sliderRef.slider.get());
  }
}



