import { Component, OnInit } from '@angular/core';
import { Weather } from '../../services/weather';
import { ProductService } from '../../services/product.service';

@Component({
  selector: 'app-utility-weather',
  templateUrl: './utility-weather.component.html',
  styleUrls: ['./utility-weather.component.css']
})
export class UtilityWeatherComponent implements OnInit {

  constructor(private productService:ProductService) { }
  private weather:Weather =null;
  ngOnInit() {
    setTimeout(()=>{   
      this.productService.getWeather().subscribe((weather:any) => {
        console.log("UREKA");
        console.log(weather);
        this.weather=Weather.make(weather);
      })
    },5000);
   
  }

}



