import { Component, OnInit } from '@angular/core';

import { SearchproductService } from '../../services/searchproduct.service';
import { Product } from '../../services/product';

@Component({
  selector: 'app-product-filter-color',
  templateUrl: './product-filter-color.component.html',
  styleUrls: ['./product-filter-color.component.css']
})
export class ProductFilterColorComponent implements OnInit {

  availableColours:String[] = null;
  constructor(private searchproductService: SearchproductService) { }
  ngOnInit() {
    this.searchproductService.filteredColoursAnnouncement$.subscribe(colours => this.availableColours=colours)
  }

  public selected($event){
   this.searchproductService.setColoursSelected($event.target.innerText);
  }

}



