import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/Subject';
import { ProductService } from '../services/product.service';
import { Product, PaginationInformation, ProductPaging, FilterCriteriaInterface, FilterCriteriaSetterInterface, FilterCriteria } from '../services/product';

@Injectable()
export class SearchproductService implements FilterCriteriaSetterInterface {
  originalProductsList: Product[] = null;
  filterCriteria: FilterCriteriaInterface = null;

  private paginationInformation:PaginationInformation=null;

  private _filteredListAnnouncement: Subject<Product[]> = new Subject<Product[]>();
  filteredListAnnouncement$ = this._filteredListAnnouncement.asObservable();

  private _filteredAvailableColoursAnnouncement: Subject<String[]> = new Subject<String[]>();
  filteredColoursAnnouncement$ = this._filteredAvailableColoursAnnouncement.asObservable();

  private _filteredAvailableTypesAnnouncement: Subject<String[]> = new Subject<String[]>();
  filteredAvailableTypesAnnouncement$ = this._filteredAvailableTypesAnnouncement.asObservable();

  private _paginationAnnouncement: Subject<PaginationInformation> = new Subject<PaginationInformation>();
  paginationAnnouncement$ = this._paginationAnnouncement.asObservable();

  private _firstAnnouncement: Subject<Product> = new Subject<Product>();
  firstAnnouncement$ = this._firstAnnouncement.asObservable();

  constructor(private productService: ProductService) { }
  initialize() {
    this.productService.getProduct().subscribe((products: any) => {
      this.originalProductsList = this.makeListOfProducts(products);
      this.filterCriteria = new FilterCriteria();
      this._filteredListAnnouncement.next(this.originalProductsList.map(x => Object.assign({}, x)));
        this.initAdditionalValues();
    })
  }
  getAllProducts() {
    if (this.originalProductsList == null) {
      this.initialize();
    }
  }
  public setRange(range: Number[]) {
    this.filterCriteria.setRange(range);
    this.applyFilter();
  }
  public setColoursSelected(coloursSelected: String) {
    this.filterCriteria.setColoursSelected(coloursSelected);
    this.applyFilter();
  }
  public setTypeSelected(typeSelected: String) {
    this.filterCriteria.setTypeSelected(typeSelected);
    this.applyFilter();
  }
  public setTextSearched(textSearched: String) {
    this.filterCriteria.setTextSearched(textSearched);
    this.applyFilter();
  }
  public applyFilter() {
    let $this = this;
    
    let theNewList: Product[] = ($this.originalProductsList.filter(product =>
      $this.checkBetween(product.getCost(), $this.filterCriteria.getRange()) &&
      $this.searchText(product.getHeaderText(), $this.filterCriteria.getTextSearched()) &&
      $this.searchColours(product.getColor(), $this.filterCriteria.getColoursSelected()) &&
      $this.searchType(product.getType(), $this.filterCriteria.getTypeSelected())
    )
      .map((product: Product, index) => {
        product.setId(index);
        return product;
      }));
    $this._filteredListAnnouncement.next(theNewList);
    this.preparePagination(theNewList);
    let theFirstProduct=theNewList!=null && theNewList.length>0?theNewList[0]:null;
    this.captureAndAnnounceForDetail(theNewList);
  }
  captureAndAnnounceForDetail(list:Product[]){
    let product=list!=null && list.length>0?list[0]:null;
    this._firstAnnouncement.next(product);
  }
  /**
   * 
   * here is the main business logic of the multi property filtering
   * @param header 
   * @param searchString 
   */
  private searchType(header: String, searchString: String) {
    return (typeof searchString === 'undefined') || searchString == null || (header.toLowerCase().indexOf(searchString.toLowerCase()) >= 0);
  }
  private searchColours(color: String[], selectedColours: String) {
     if (typeof selectedColours === 'undefined' || selectedColours == null)
      return true;

      let fcColours=new Set(selectedColours)
      let prdColours=new Set(color);
      let retVal=false;
      prdColours.forEach(elem=>{
        retVal=retVal || fcColours.has(<string>elem)
      })
      return retVal;
  }
  private searchText(text: String, searchString: String): Boolean {
    return (typeof searchString === 'undefined') || searchString === null || text.toLowerCase().indexOf(searchString.toLowerCase()) >= 0;
  }
  private checkBetween(cost: Number, range: Number[]): Boolean {
    return (typeof range === 'undefined') || range == null || (cost > range[0] && cost < range[1])

  }
  
  colorSet: Set<String> = null;
  typeSet: Set<String> = null;

  private initAdditionalValues(): void {
    this.colorSet = new Set<String>();
    this.typeSet = new Set<String>();
    let $this = this;
    this.originalProductsList.forEach(product => {
      product.getColor().forEach(color => $this.colorSet.add(color))
      $this.typeSet.add(product.getType());
    })
    this._filteredAvailableColoursAnnouncement.next(Array.from(this.colorSet));
    this._filteredAvailableTypesAnnouncement.next(Array.from(this.typeSet));
    this.preparePagination(this.originalProductsList);
    this.captureAndAnnounceForDetail(this.originalProductsList);
  }
  preparePagination(theList:Product[]){
    let total=theList.length;
    this.paginationInformation=new PaginationInformation(0, total>3?3:total, total);
    this.announce(this.paginationInformation);
    this._firstAnnouncement.next(theList[this.paginationInformation.getStartPage()]);
  }
  private makeListOfProducts(prodJSON: any): Product[] {
    let retList: Product[] = [];
    prodJSON.forEach((prodJsonUnit,index) => {
      let p=new Product(prodJsonUnit);
      p.setId(index);
      retList.push(p)
    });
    return retList;
  }
  private announce(pinfo:PaginationInformation){
    this._paginationAnnouncement.next(pinfo);
  }
  public next(){
    if(this.paginationInformation.next())
    this.paginationAndDetailAnnouncement()
  }
  public prev(){
    if(this.paginationInformation.prev())
      this.paginationAndDetailAnnouncement();
  }
  private paginationAndDetailAnnouncement(){
    this.announce(this.paginationInformation);
    this._firstAnnouncement.next(this.originalProductsList[this.paginationInformation.getStartPage()]);
   }
   selectedOverviewManually(product:Product){
    this._firstAnnouncement.next(product);
    }
}



