import { Component, OnInit } from '@angular/core';

import { SearchproductService } from '../../services/searchproduct.service';
import { Product } from '../../services/product';

@Component({
  selector: 'app-product-filter-type',
  templateUrl: './product-filter-type.component.html',
  styleUrls: ['./product-filter-type.component.css']
})
export class ProductFilterTypeComponent implements OnInit {

  availableTypes:String[] = [];
  constructor(private searchproductService: SearchproductService) { }
  ngOnInit() {
   this.searchproductService.filteredAvailableTypesAnnouncement$.subscribe(types => 
      {
      //console.log(types);
      //console.log("************-----------");
      types.forEach(item => this.availableTypes.push(item));
      //console.log(this.availableTypes);
   })
  }
  selectThis(event):void{
    //console.log(event)
    if(typeof event.textContent !== 'undefined' && event.textContent !==null)
      this.searchproductService.setTypeSelected(new String(event.textContent).trim());
  }
}



