import { Component, OnInit } from '@angular/core';
import { SearchproductService } from '../../services/searchproduct.service';
import { Product , PaginationInformation} from '../../services/product';


@Component({
  selector: 'app-product-detail',
  templateUrl: './product-detail.component.html',
  styleUrls: ['./product-detail.component.css']
})
export class ProductDisplayComponent implements OnInit {
  private product:Product=null;
  constructor(private searchproductService: SearchproductService) {}
  private paginatedInfo:PaginationInformation=null;
  ngOnInit() {
    this.searchproductService.paginationAnnouncement$.subscribe(
      (paginatedInfo:PaginationInformation) => {
       this.paginatedInfo=paginatedInfo;
      }
    )
    this.searchproductService.firstAnnouncement$.subscribe(product=> this.product=product)
  }
public next(event):void{
  this.searchproductService.next();
  }
  public prev(event):void{
    this.searchproductService.prev();
    }
}



