import { Component, OnInit, ElementRef } from '@angular/core';
import { SearchproductService } from '../../services/searchproduct.service';
import { Product , PaginationInformation} from '../../services/product';

@Component({
  selector: 'app-product-overview',
  templateUrl: './product-overview.component.html',
  styleUrls: ['./product-overview.component.css']
})
export class ProductOverviewComponent implements OnInit {
  el: ElementRef;

  private products: Product[] = null;
  public trimmedProducts:Product[] =null;
  constructor(private searchproductService: SearchproductService, el: ElementRef) { 
    this.el = el; 

  }
  ngOnInit() {
    this.searchproductService.filteredListAnnouncement$.subscribe(
      (products: Product[]) => {
        this.products = products;
      }
    );

   

    this.searchproductService.paginationAnnouncement$.subscribe(
      (paginatedInfo:PaginationInformation) => {
        //console.log(paginatedInfo.getStartPage() +" :: "+paginatedInfo.getEndPage());
        this.trimmedProducts=this.products.slice(paginatedInfo.getStartPage(), paginatedInfo.getEndPage()+1).map(p => new Product(p))
       console.log("the temp ");
       console.log(this.trimmedProducts);
      }
    )
    this.searchproductService.getAllProducts() 
    }
  public triggerDetailSelection(event):void{
    var target = event.target || event.srcElement || event.currentTarget;
    var id = target.id;
    let theSelectedProduct=this.searchproductService.originalProductsList[id];
    this.searchproductService.selectedOverviewManually(theSelectedProduct);
  }
}



