import { Component, OnInit } from '@angular/core';

import { SearchproductService } from '../../services/searchproduct.service';
import { Product } from '../../services/product';


@Component({
  selector: 'app-product-filter-text',
  templateUrl: './product-filter-text.component.html',
  styleUrls: ['./product-filter-text.component.css']
})
export class ProductFilterTextComponent implements OnInit {

  constructor(private searchproductService: SearchproductService) { }

  ngOnInit() {
  }

  public searchTextChanged(value:String):void{
    if(typeof value !=='undefined')
    this.searchproductService.setTextSearched(value);
  }
}



